/* footer
*******************************************************************/

footer {
    padding: 60px 0 0;
    background: rgba($app-color-3,.06);
    h5 {
        margin-bottom: 30px;
    }
    p {
        font-size: 18px;
        margin-bottom: 30px;
        font-weight: 300;
    }

    .nav {
        display: block;
        .nav-item {
            display: block;
            .nav-link {
                color: $gray-800;
                padding: 8px 0;
                border-bottom: 1px solid $gray-800;
            }
        }
    }

    .social-nav {
        img {
            width: 40px;
            height: auto;
            margin-right: 15px;
        }
    }

    .extraImg {
        width: 55px;
        height: auto;
    }

    .bottombar {
        background: $app-color-3;
        padding: 15px 0;
        color: $white;
        a {
            color: $white;
        }
    }

}
